@use '../setting' as *;
@use 'function' as *;

// SCSS使用例
// @include fz(12,15,400,0.3);
// @include fzRem(24,29,600);

// 01__フォントサイズ，ラインハイト(ハーフリーディングを除く)，レタースペーシング，まとめて指定
// ----------------------------------------------------------
@mixin fz($size: 24, $height: false, $weight: false, $space: false, $base: 16) {

  font-size: #{$size}px;
  $lineheight: division($height,$size);

  @if $weight {
    font-weight: $weight;
  }

  @if $height {
    line-height: $lineheight;

    &::before,
    &::after {
      display: block;
      width: 0;
      height: 0;
      content: "";
    }
    
    &::before {
      margin-top: calc((1 - #{$lineheight}) * 0.5em);
    }
    
    &::after {
      margin-bottom: calc((1 - #{$lineheight}) * 0.5em);
    }
  }

  @if $space {
    letter-spacing: convertLsp($space,$size);
  }
}

