@mixin fzRem(
  $remove_halfReading: true,
  $size: 24,
  $height: false, 
  $weight: false, 
  $space: false, 
  $base: 16
) {  
  font-size: convertRem($size);
  $lineheight: division($height,$size);

  @if $weight {
    font-weight: $weight;
  }

  @if $height {
    line-height: $lineheight;

    @if $remove_halfReading {
      &::before,
      &::after {
        display: block;
        width: 0;
        height: 0;
        content: "";
      }
      
      &::before {
        margin-top: calc((1 - #{$lineheight}) * 0.5em);
      }
      
      &::after {
        margin-bottom: calc((1 - #{$lineheight}) * 0.5em);
      }
    }
  }
    
  @if $space {
    letter-spacing: convertLsp($space,$size);
  }
}
