@use '../global' as *;

%text {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: transparent;
  background-image: none;
  border: 1px solid $cr_form_bd;
  border-radius: 0;
  color: inherit;
  font-family: inherit;
  font-size: 1em;
  padding: 0.23em 0.7em;
	width: 100%;
	
	&:focus {
  	border: 1px solid rgba(0, 0, 0, 0.32);
  	box-shadow: none;
		outline: none;
	}
}

%textarea {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: transparent;
  background-image: none;
  border: 1px solid $cr_form_bd;
  border-radius: 0;
  color: inherit;
  font-family: inherit;
  font-size: 1em;
  height: 196px;
  padding: 0.4em 0.8em;
	width: 100%;

	@include mq("sm") {
		height: 164px;
	}
	
	&:focus {
  	border: 1px solid rgba(0, 0, 0, 0.32);
  	box-shadow: none;
  	outline: none;
	}
}

%radio {
  display: none;
	
	+ span {
		cursor: pointer;
		display: inline-block;
		margin: 0 0.2em 0;
		padding: 0 0 0 1.875em;
		position: relative;

		&::before {
			-webkit-transform: translateY(-50%);
			background: #fff;
			border: 1px solid rgba(0, 0, 0, 0.16);
			border-radius: 50%;
			content: "";
			display: block;
			height: $size_form_radio;
			left: 0;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			width: $size_form_radio;
		}

		&::after {
			-webkit-transform: translateY(-50%);
			background: $cr_form_radio;
			border: 1px solid transparent;
			border-radius: 50%;
			content: "";
			height: 6px;
			left: 3.1px;
			opacity: 0;
			// padding: 2px;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			transition: all 0.3s ease 0s;
			width: 6px;
		}
		&:focus,
		&:hover {
			&::before {
				border: 1px solid $cr_blue;
			}
		}
		&:active {
			&::before {
				border: 1px solid $cr_blue;
				background-color: $cr_blue;
				opacity: .2;
			}
		}
	}
	
	&:checked + span::after {
		opacity: 1;
	}
}
	
%checkbox {
  display: none;
	
	+ span {
		cursor: pointer;
		display: inline-block;
		margin: 0 0.2em 0;
		padding: 0 0 0 $pl_form_checkbox_txt;
		position: relative;
		
		&::before {
			-webkit-transform: translateY(-50%);
			background: #fff;
			border: 1px solid rgba(0, 0, 0, 0.16);
			border-radius: 2px;
			content: "";
			display: block;
			left: -4px;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			height: $size_form_checkbox;
			width: $size_form_checkbox;
			@include mq("sm") {
				height: $size_form_checkbox/1.2;
				width: $size_form_checkbox/1.2;
			}
		}
		
		&::after {
			-webkit-transform: translateY(-50%) rotate(-45deg);
			border-bottom: $lineWeight_form_checkbox_check solid  $cr_form_checkbox_check;
			border-left: $lineWeight_form_checkbox_check solid $cr_form_checkbox_check;
			content: "";
			display: block;
			height: $lengthH_form_checkbox_check;
			width: $lengthW_form_checkbox_check;
			margin-top: -0.2em;
			opacity: 0;
			position: absolute;
			top: 50%;
			left: -5px;
			transform: translateY(-50%) rotate(-45deg);
			transition: all 0.3s ease 0s;
			@include mq("sm") {
				margin-top: -0.1em;
				height: $lengthH_form_checkbox_check/1.2;
				width: $lengthW_form_checkbox_check/1.2;
			}
		}

		&:focus,
		&:hover {
			&::before {
				border: 1px solid $cr_blue;
				box-shadow: 0 0 5px rgba(73, 115, 255, 0.2);
			}
		}
		&:active {
			&::before {
				border: 1px solid $cr_blue;
				background-color: $cr_blue;
				opacity: .2;
			}
		}
	}
		
	&:checked + span::after {
			opacity: 1;
	}
}

%select {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background: transparent url(https://haniwaman.com/wp/wp-content/uploads/2018/12/form-css-arrow.png) no-repeat center right 13px/12px 12px;
  border: 1px solid $cr_form_bd;
  border-radius: 0;
  color: inherit;
  cursor: pointer;
  font-family: inherit;
  font-size: 1em;
  padding: 0.54em 0.7em;
  width: 100%;
	
	&::-ms-expand {
		display: none;
	}
	
	&:focus,
	&:hover {
		// border: 1px solid rgba(0, 0, 0, .32);
		box-shadow: 0 0 4px $cr_blue;
		outline: none;
	}
}

%submitBtn {
  -webkit-appearance: none;
  background-color: rgba(0, 0, 0, 0.32);
  background-image: none;
  border: none;
  border-radius: 0;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 1em;
  margin: 0;
  padding: 0.6em 2em;
  text-decoration: none;
	
	&:focus,
	&:hover {
		outline: none;
	}
	
	&::-moz-foucus-inner {
		border: none;
		padding: 0;
	}
}

%file {
	display: none;
}
