@use '../setting' as *;

//メディアクエリ用のmixinを定義。デフォ値はmd
@mixin mq($breakpoint: sm) {
  //map-get(マップ型変数, キー)で値を取得
  @media #{map-get($breakpoints, $breakpoint)} {
    //この中をカスタムできる
    @content;
  }
}

@mixin background(
  $color: transparent,
  $image,
  // 呼び出し側：ulr(../img/)など
  $repeat: no-repeat,
  $position-x: center,
  $position-y: center,
  $size: cover
) {
  background: {
    color: $color;
    image: $image;
    repeat: $repeat;
    position: $position-x $position-y;
    size: $size;
  }
}

@mixin center {
  text-align: center;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

@mixin centering-elements($horizontal: true, $vertical: true) {
  position: absolute;
  @if $horizontal and $vertical {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  } @else if $horizontal {
    left: 50%;
    transform: translate(-50%, 0);
  } @else if $vertical {
    top: 50%;
    transform: translate(0, -50%);
  }
}
// 05__画像の縦横比を設定
// ----------------------------------------------------------
@mixin imgWrapper_ratio($width, $height) {
  position: relative;
  padding-top: ($width / $height) * 100%;
  overflow: hidden;
  > img {
    position: absolute;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
  }
}

// 05__ブロックの縦横比を設定
// ----------------------------------------------------------
@mixin aspect_ratio($width, $height, $image_path: null) {
  width: 100%;
  padding-top: ($height / $width) * 100%;
  position: relative;

  @if $image_path != null {
    background: url($image_path) 50% 50% no-repeat;
    background-size: cover;
  }

  & > :first-child {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: none;
  }
}
// 06__ulの余白 ulに対してCSS指定
// ----------------------------------------------------------
@mixin bulletList_margin($bottom: 0, $right: 0) {
  > *:last-child {
    margin-bottom: 0;
  }
  > li {
    margin-bottom: #{$bottom}px;
  }

  @if $right {
    > *:last-child {
      margin-right: 0;
    }
    > li {
      margin-right: #{$right}px;
    }
  }
}


// 06__横並びカード(boostnote登録済)
// ----------------------------------------------------------
@mixin unit_col ($col:3, $right:30, $bottom:0) {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: -#{$bottom}px;
  

  > .bl_card, .bl_halfMedia, li {
    width: calc(100% / #{$col} - #{$right}px * (#{$col} - 1) / #{$col});
    // }
    
    margin-right: #{$right}px;
    margin-bottom: #{$bottom}px;
    
    
    @if ($col == 3) {
      &:nth-of-type(3n) { 
        margin-right: 0;
        // @include mq("md") { margin-right: #{$right}px; }
      }
      &:nth-of-type(2n) {
        // @include mq("md") { margin-right: 0px; }
      }
      // @include mq("md") { width: calc(50% - 24px * 1 / 2); }
      @include mq("sm") {
        width: 100%;
        margin-right: 0;
      }
    }

    @if ($col == 2) {
      &:nth-of-type(2n) { margin-right: 0; }
      @include mq("sm") {
        width: 100%;
        margin-right: 0;
      }
    }
  }
}

// 06__ulの余白
// ----------------------------------------------------------
@mixin ly_flex_fix ($width){
  display: flex;
  & > *:first-child {
    flex: 0 0 #{$width}px;
  }
  & > :nth-child(2) {
    flex: 0 1 100%;
  }
}

@mixin ly_flex_center ($width){
  display: flex;
  align-items: flex-start;
  @include mq("sm") {
    display: block;
  }
  & > :first-child {
    flex: 0 1 #{$width};
    margin-right: 3.333%;
    @include mq("sm") {
      margin-right: auto;
      margin-left: auto;
    }
  }
  & > :nth-child(2) {
    flex: 1;
  }
}
// 09__疑似要素初期化
// ----------------------------------------------------------
@mixin icon-ini {
  content: '';
  display: inline-block;
}

// 07__くの字矢印
// ----------------------------------------------------------
@mixin arrow($width, $bold, $color, $deg) {
  position: absolute;
  content: '';
  display: inline-block;
  border-color: $color;
  border-style: solid;
  border-width: 0 #{$bold}px #{$bold}px 0;
  height: #{$width}px;
  vertical-align: middle;
  width: #{$width}px;
  top: 50%;
  //角度
  @if ($deg == 'left') {
      transform: translateY(-50%) rotate(135deg);
  }

@else if ($deg == 'top') {
      transform:  translateY(-50%) rotate(225deg);
  }

@else if ($deg == 'right') {
      transform: translateY(-50%) rotate(-45deg);
  }

@else if ($deg == 'bottom') {
      transform: translateY(-50%) rotate(45deg);
  }

  // @include ie11only() {
  //     margin-bottom: 1px !important;
  // }
}

@mixin arrowl($width, $bold, $color, $deg) {
  border-style: solid;
  border-width: 0 #{$bold}px #{$bold}px 0;
  content: "";
  display: inline-block;
  position: absolute;
  height: #{$width}px;
  width: #{$width}px;
  //色
  @if ( $color == "blue" )        { border-color: #0067C0; }
  @else if ( $color == "white" )  { border-color: #fff;    }
  @else if ( $color == "glay" )   { border-color: #808080; }
  @else if ( $color == "black" )  { border-color: #000; }
  //角度
  @if ( $deg == "left" )        { transform: rotate(135deg); }
  @else if ( $deg == "top" )    { transform: rotate(225deg); }
  @else if ( $deg == "right" )  { transform: rotate(-45deg); }
  @else if ( $deg == "bottom" ) { transform: rotate(45deg);  }
  //SPの時
  @include mq(sm) {
      height: #{$width/2}px;
      width: #{$width/2}px;
  }
}