}

.el_lv2Heading {
  padding-bottom: 10px;
  border-bottom: 4px solid #e25c00;
  @include fzRem(28,42,bold);
}

.el_lv3Heading {
  border-left: 5px solid #3f51b5;
  padding-left: 6px;

  @include fzRem(28,42,bold);
}

.el_lv4Heading {
  display: inline-block;
  @include fzRem(28,42,bold);
  background: linear-gradient(transparent 50%, #dcee39 0);
}
.el_lv5Heading {
  font-size: 1.75rem;
  span {
    display: inline-block;
    background: #dcee39;
    @include fz(28,42,bold);
  }
}

.el_lv6Heading {
  font-size: 1.75rem;
  background: #3f51b5;
  color: #fff;
  padding: 10px 0 2px 10px;
  @include fz(28,42,bold);
  position: relative;
  &::after {
    position: absolute;
    content: "";
    width: 0;
    height: 0;
    border: 15px solid transparent;
    border-top: 15px solid #3f51b5;
    left: 30px;
    top: 100%;
  }
}

.el_lv7Heading {
  @include fzRem(28,42,bold);
  border-bottom: 2px solid #3f51b5;
  position: relative;
  &::after {
    position: absolute;
    content: "";
    border-bottom: 2px solid #b53f4f;
    bottom: 5px;
    left: 0;
    width: 30%;
    height: 100%;
  }
}


.el_lv8Heading {
  @include fzRem(28,42,bold);
  padding-left: 30px;
  position: relative;
  
  &::before {
    font-family: "Font Awesome 5 Free";
    content: "\f00c";
    position: absolute;
    color: #3f51b5;
    font-weight: 900;
    font-size: 0.7em;
    left: 0;
    top: 20%;
    transform: translateY(-50%);
  }
}

.el_lv9Heading {
  display: inline-block;
  @include fzRem(28,42,bold);
  padding-right: 10px;
  position: relative;
  
  &::after {
    font-family: "Font Awesome 5 Free";
    content: "\f002";
    position: absolute;
    top: -50%;
    right: 0;
    color: #3f51b5;
    font-weight: 900;
    font-size: 0.8em;
    transform: translateY(-50%);
  }
}

.el_lv10Heading {
  display: inline-block;
  position: relative;
  @include fzRem(28,42,bold);
  padding: 0 30px;
  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 3px;
    height: 32px;
    background: #3f51b5;
    top: 50%;
    transform: translateY(-50%);
  }

  &::before {
    left: 0;
    transform: translateY(-50%) rotate(-10deg);
  }

  &::after {
    right: 0;
    transform: translateY(-50%) rotate(10deg);
  }
}

.el_lv11Heading {
  display: flex;
  font-size: 1.75rem;
  font-weight: bold;
  align-items: center;
  &::before,
  &::after {
    content: "";
    height: 3px;
    background: #3f51b5;
    flex-grow: 1;
  }
  &::before {
    margin-right: 12px;
  }
  &::after {
    margin-left: 12px;
  }
}

.el_lv12Heading {
  display: inline-block;
  @include fzRem(28,42,bold);
  padding: 2px 10px;
  background: -webkit-repeating-linear-gradient(
    -45deg,
    #a7baff,
    #a7baff 15px,
    #7386e9 0px,
    #7386e9 30px
  );
  background: repeating-linear-gradient(
    -45deg,
    #a7baff,
    #a7baff 15px,
    #7386e9 0px,
    #7386e9 30px
  );
}

.el_lv13Heading {
  display: inline-block;
  font-size: 1.75rem;
  font-weight: bold;
  padding: 2px 10px;
  position: relative;

  > span {
    position: relative;
    z-index: 3;
  }

  &::after {
    content: "";
    background: repeating-linear-gradient(
      -45deg,
      #a7baff,
      #a7baff 15px,
      #7386e9 0,
      #7386e9 30px
    );

    position: absolute;
    bottom: 0;
    left: 0;
    height: 50%;
    width: 100%;
    z-index: 2;
  }
}