@use '../setting' as *;

/* $targetFontSizeにはremに変換したいpxの数字が入る */
@function convertRem($targetFontSize) {
	@return ($targetFontSize / 16) + rem;
}

// letter-spacing計算
@function convertLsp($targetLetter_spacing, $fontSize) {
	@return $targetLetter_spacing / $fontSize + em;
}

/* 
画面サイズ375pxの時に16pxで表現されているfont-sizeをvwで表現したい時
 $targetSize・・・vwに指定したいサイズ。今回の例でいうと16pxが当てはまる
 $windowSize・・・画面サイズ。今回の例でいうと375pxが当てはまる。
*/

@function convertVw($targetSize, $windowSize) { 
	@return ($targetSize / $windowSize * 100vw);
}

// 割り算
@function division($numerator, $denominator) {
	@return $numerator / $denominator;
}

// 割り算%
@function divisionP($numerator, $denominator) {

	@return $numerator / $denominator * 100%;
}


// 背景色の明度によって文字色を変える
@function color-from-background($background-color) {
	$color: $color-text;

	@if lightness($background-color) < 70% {
		$color: #fff;
	}

	@return $color;
}