@use '../global' as *;

.el_btn {
  display: inline-block;
  // width: 320px;
  max-width: 100%;
  padding: 20px 45px;
  background-color: $btn_bg;
  border: 2px solid transparent;
  color: $btn_color;
  @include fzRem(true, 14, 22);
  text-align: center;
  text-decoration: none;
  transition: $transition;

  &:focus,
  &:hover {
    background-color: $btn_color;
    border-color: currentColor;
    color: $btn_bg;
    cursor: pointer;
  }

  &.el_btn__s {
		font-weight: 600;
  	padding: 8px 30px;
  	border-width: 5px;
  }

  &.el_btn__rev {
    // background-color: $btn_color;
    background-color: transparent;
    color: $btn_bg;
    border-color: $btn_bg;

    &:focus,
    &:hover {
      background-color: $btn_bg;
      color: $btn_color;
    }
  }

  &.el_btn__brawn {
    background-color: #603813;
    color: #fff;

    &:focus,
    &:hover {
      background-color: #fff;
      border-color: #603813;
      color: #603813;
    }
  }
}

// アロー
.el_btn.el_btn__arrowRight {
  position: relative;
  padding-right: 2em;
  padding-left: 1.38em;

  &::after {
    content: "\f054";
    position: absolute;
    top: 50%;
    right: 0.83em;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    transform: translateY(-50%);
  }
}

.el_btn.el_btn__large {
  width: 340px;
  padding-top: 25px;
  padding-bottom: 25px;
  font-size: 1.375rem;
}
.el_btn.el_btn__brawn {
  background-color: #603813;
  // color: #222;

  &:focus,
  &:hover {
    background-color: #fff;
    border-color: #603813;
  }
}

.el_roundedBtn {
  display: inline-block;
  // width: 236px;
  max-width: 100%;
  padding: 14px 48px;
  background-color: $bgc_roundedBtn;
  border: 2px solid transparent;
  border-radius: 10px;
  color: #fff;
  @include fzRem(18, 26, 700, 0.45);
  text-align: center;
  text-decoration: none;
  transition: 0.25s;

  @include mq("sm") {
    @include fzRem(16, 24, 700, 0);
    padding: 14px 30px;
  }

  @include mq("md") {
    &:not(.-submit) {
      &:focus,
      &:hover {
        background-color: $bgc_roundedBtn_active;
      }
    }
  }
  &:disabled {
    background-color: #b2b2b2;
    cursor: initial;
  }
}

.el_beforeIconBtn {
  position: relative;
  display: inline-block;
  padding: 0.2em 0.3em;
  border: 1px solid currentColor;
  color: #e25c00;
  text-decoration: none;
  transition: 0.25s;

  &:focus,
  &:hover {
    background-color: #e25c00;
    color: #fff;
    cursor: pointer;
  }

  &::before {
    display: inline-block;
    margin-right: 0.5em;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
  }

  &.el_beforeIconBtn__download::before {
    content: "\f019";
  }

  &.el_beforeIconBtn__zoom::before {
    content: "\f00e";
    transform: translateY(-6%);
  }
}

