@use '../global' as *;

.el_label {
  display: inline-block;
  padding: .2em .3em;
  background-color: $bgc_label;
  color: $cr_label;
  @include fzRem(12, 20, bold);
  border: 2px solid transparent;

  &.el_label__blue,
  &[data-text="ラベル2"] {
    background-color: $bgc_label;
  }
  &.el_label__red,
  &[data-text="ラベル3"] {
    background-color: #e81919;
  }
}

a.el_label {
  text-decoration: none;
  transition: all 0.3s ease 0s;

  &:focus,
  &:hover {
    cursor: pointer;
    background-color: #fff;
    border-color: currentColor;
    color: #666;
  }

  &.el_label__blue {
    &:focus,
    &:hover {
      color: $cr-p;
    }
  }

  &.el_label__red {
    &:focus,
    &:hover {
      color: #e81919;
    }
  }
}

.el_roundedLabel {
  display: inline-block;
  padding: .42em 2.2em;
  background-color: $bgc_label;
  color: $cr_label;
  border-radius: 1em;
  @include fzRem(14,26,400,0.35);
  border: 2px solid $cr_label_border;
  white-space: nowrap;
  @include mq("sm") {
    @include fzRem(12, 24, 400, 0.3);
    padding: .12em 1.2em;
  }
}