// レイアウト幅
$ly_width_inner: 1140px;

// メディアクエリの基準幅
$bp_sm: 768px;
$bp_md: 992px;
$bp_lg: 1200px;

// ブレークポイント
$breakpoints: (
  "sm": "screen and (max-width: 767px)",
	"md": "screen and (min-width: 768px) and (max-width: 1199px)",
	"lg": "screen and (min-width: 1200px)"
	// "md": "screen and (max-width: 1199px)",
	// "lg": "screen and (max-width: px)",
	// "md": "only screen and (min-width: 768px ) and (max-width: 991px)",
	// "lg": "only screen and (min-width: 992px ) and (max-width: 1199px)",
  // "sm": "screen and (max-width: #{$bp_sm} - 1px)",
	// "md_only": "(min-width: #{$bp_sm}) and (max-width: #{$bp_md} - 1px)",
	// "md": "screen and (max-width: #{$bp_md} - 1px)",
	// "md": "screen and (max-width: #{$bp_md} - 1px)",
	// "lg": "screen and (max-width: #{$bp_lg} - 1px)",
) ;


// ヘッダー高さ
$header_height_sm: 60px;
$header_height: 90px;

// z-index の指定
$layer: (
	modal: 100,
	drawer: 40,
	floating: 30,
	header: 20,
	footer: 10,
	default: 1
);

// 余白
$pd_x_sm: 16px;
$pd_x_md: 24px;
$pd_x_lg: 40px;

// 余白
$pd_y_sm: 40px;
$pd_y_md: 24px;
$pd_y_lg: 60px;