@use '../global' as *;

header {
	z-index: map-get($layer, "header");
}

.ly_header {
	background: $header_bg;
	color: $header_color;
	height: $header_height;
	width: 100%;

	@include mq("sm") {
		height: $header_height_sm;
  }

	> .ly_inner {
		max-width: $ly_width_inner;
		margin-left: auto;
		margin-right: auto;
		box-sizing: content-box;

	}
}

